<template>
  <div>
    <el-card>
      <el-form
        label-width="80px"
        :model="RegisterForm"
        label-position="left"
        :rules="RegisterFormRules"
        ref="RegisterFormRef"
        status-icon
      >
        <el-form-item label="" prop="username">
          <el-input v-model="RegisterForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="RegisterForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickname">
          <el-input v-model="RegisterForm.nickname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="RegisterForm.email"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload class="upload-demo" :action="uploadUrl" :on-success="afterUpload">
            <el-button size="small" type="primary"></el-button>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" style="float: right" @click="register" round></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (regEmail.test(value)) {
        return cb();
      }

      cb(new Error(""));
    };

    return {
      RegisterForm: {},
      imageUrl: "",
      //
      RegisterFormRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3-10",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "6-15",
            trigger: "blur",
          },
        ],
        nickname: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: "3-15",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    //
    afterUpload(res) {
      this.$set(this.RegisterForm, "avatar", res.url);
    },
    //
    async register() {
      //
      this.$refs.RegisterFormRef.validate(async (valid) => {
        if (!valid) return;
        let res;
        res = await this.$http.post("user/create", this.RegisterForm);
        this.$router.push("/login");
      });
    },
  },
};
</script>

