<template>
  <div>
    <el-card>
      <el-form
        label-width="80px"
        ref="loginFormRef"
        :model="LoginForm"
        label-position="left"
        :rules="loginFormRules"
        status-icon
      >
        <el-form-item label="" prop="username">
          <el-input v-model="LoginForm.username" prefix-icon="iconfont icon-yonghu"></el-input>
        </el-form-item>
        <el-form-item type="password" label="" prop="password">
          <el-input v-model="LoginForm.password" type="password" prefix-icon="iconfont icon-mima"></el-input>
        </el-form-item>
        <div style="text-align: center; font-size: 20px">
          <span>?</span>
          <router-link to="/register" tag="span" style="color: red; font-size: 20px"></router-link>
        </div>
        <el-form-item>
          <el-button type="primary" style="float: right" @click="login" round></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  inject: ["reload"], //reload
  data() {
    return {
      LoginForm: {},
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        // 
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    async login() {
      //
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        const res = await this.$http.post("login", this.LoginForm);
        //token
        localStorage.token = res.data.token;
        //
        localStorage.userPermission = JSON.stringify(res.data.userinfo);
        this.$message({
          type: "success",
          message: "",
        });
        //
        this.$router.push("/homecontent");
        //
        this.reload();
      });
    },
  },
};
</script>

